body {
    background-color: black;
    font-family:'Open Sans';
    margin: 0;
}
@import "scss/header";


// First Effets Box
.box-cont {
    padding: 100px 0 60px ;
    .container {
        .row {
            .box {
                position: relative;
                height: 400px;
                margin-top: 50px;
                background: #060c21;
                display: flex;
                justify-content: center;
                align-items: center;
                &:hover .content h2 {
                    top: -140px;
                }
                &:hover .content a {
                    transform: translateY(0);
                    opacity: 1;
                    visibility: visible;
                }
                &::before {
                    content: "";
                    position: absolute;
                    top: -2px;
                    left: -2px;
                    right: -2px;
                    bottom: -2px;
                    background: #fff;
                    transform: skew(2deg,2deg);
                    z-index: -1;
                }
                &::after {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 50%;
                    height: 100%;
                    background: rgba(255, 255, 255, .05);
                    pointer-events: none;
                }
                
                .content {
                    position: relative;
                    padding: 20px;
                    transform: translateY(40px);
                    h2 {
                        position: absolute;
                        top: -60px;
                        right: 20px;
                        margin: 0;
                        padding: 0;
                        font-size: 10em;
                        color: rgba(255, 255, 255, .05);
                        transition: 0.5s;
                    }
                    h3 {
                        margin: 0 0 10px;
                        padding: 0;
                        font-size: 1.5em;
                        font-weight:bold;
                        color: #fff;
                    }
                    p {
                        margin: 0;
                        padding: 0;
                        color: #fff;
                        font-size: 18px;
                        font-weight: 400;
                    }
                    a {
                        position: relative;
                        margin: 20px 0 0;
                        padding: 10px 20px;
                        text-decoration: none;
                        border: 1px solid #fff;
                        display: inline-block;
                        color: #fff;
                        transition: 0.5s;
                        transform: translateY(-40px);
                        opacity: 0;
                        visibility: hidden;
                        &:hover {
                            background-color: #fff;
                            color: #000;
                        }
                    }
                }
            }
            .one::before {
                transform: skew(1deg,2deg);
                background: linear-gradient(315deg,#ff0057,#e64a19);
                top: -5px;
                left: -7px;
                right: 7px;
                bottom: 7px;
            }
            .tow::before {
                transform: skew(2deg,2deg);
                background: linear-gradient(315deg,#ff0000,#ffc107);
                top: -2px;
                left: -2px;
                right: -2px;
                bottom: -2px;
            }
            .three::before {
                transform: skew(-2deg,-2deg);
                background: linear-gradient(315deg,#e91e63,#5d02ff);
                top: -2px;
                left: -2px;
                right: -2px;
                bottom: -2px;
            }
            .four::before {

                transform: skew(-1deg,-2deg);
                background: linear-gradient(315deg,#89ff00,#00bcd4);
                top: -5px;
                left: 7px;
                right: -7px;
                bottom: 7px;
            }
        }
    }
}


// Swiper
.swiper-cont-2 {
    background-color: #fff;
    position: relative;
    .swiper{
        width: 100%;
        height: 400px;
        display: block;
        position: relative;
        .swiper-container{
        width: 300px;
        height: 300px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -150px;
        margin-top: -150px;
        .swiper-wrapper{
            margin-bottom: 20px;
            .swiper-slide {
                background-position: center;
                background-size: cover;
                color: white;
                text-align: center;
                h2 {
                    font-size: 2.5em;
                    font-weight: bold;
                    margin: 30px 0;
                }
                span {
                    font-size: 1.5em;
                    color: red;
                    width: 50px;
                    height: 30px;
                    background-color: rgba(255, 0, 0, 0.5);
                    border-radius: 20px;
                    padding: 10px;
                    color: white;
                }
                p {
                    font-size: 1.5em;
                    
                }
            }
        }
        }
    }
}
//Swiper 2
.swiper-cont-3 {
    .swiper-container {
        width: 100%;
        padding-top: 50px;
        padding-bottom: 50px;
    }
    .swiper-slide {
        background-position: center;
        background-size: cover;
        width: 300px;
        height: 300px;
    }
    .swiper-pagination-bullet {
        background:#fff
    }
}